import React, { useState } from "react";
import Chord1 from "./Chord1.jpg";
import a from "./a.png";
import ReactAudioPlayer from "react-audio-player";
import D from "./D.mp3";

function Guitarquiz() {
	const questions = [
		{
			questionText: "What is this chord shape?",
			answerOptions: [
				{ answerText: "A", isCorrect: false },
				{ answerText: "D", isCorrect: false },
				{ answerText: "C", isCorrect: true },
				{ answerText: "E", isCorrect: false },
			],
		},
		{
			questionText: "What type of chord do you heard?",
			answerOptions: [
				{ answerText: "G", isCorrect: false },
				{ answerText: "D", isCorrect: true },
				{ answerText: "Am", isCorrect: false },
				{ answerText: "Dm", isCorrect: false },
			],
		},
		{
			questionText: "What are the notes of a F major chord?",
			answerOptions: [
				{ answerText: "C-E-G", isCorrect: false },
				{ answerText: "C-Eb-F", isCorrect: false },
				{ answerText: "F-A-C", isCorrect: true },
				{ answerText: "C-F-G", isCorrect: false },
			],
		},

		{
			questionText: "What is this scale?",
			answerOptions: [
				{ answerText: "A major", isCorrect: true },
				{ answerText: "D major", isCorrect: false },
				{ answerText: "C lydian", isCorrect: false },
				{ answerText: "E dorian", isCorrect: false },
			],
		},

		{
			questionText: "How many sharps are in a G scale?",
			answerOptions: [
				{ answerText: "3", isCorrect: false },
				{ answerText: "1", isCorrect: true },
				{ answerText: "5", isCorrect: false },
				{ answerText: "2", isCorrect: false },
			],
		},
	];

	const [currentQuestion, setCurrentQuestion] = useState(0);
	const [showScore, setShowScore] = useState(false);
	const [score, setScore] = useState(0);

	const handleAnswerOptionClick = (isCorrect) => {
		if (isCorrect) {
			setScore(score + 1);
		}

		const nextQuestion = currentQuestion + 1;
		if (nextQuestion < questions.length) {
			setCurrentQuestion(nextQuestion);
		} else {
			setShowScore(true);
		}
		


	};

	return (
		<div className="app">
			{showScore ? (
				<div className="score-section">
					You scored {score} out of {questions.length} 
				
					{score  === 0 &&
        <h3	>
         "Maybe consider coding as a career path?"
        </h3>
      }
				
					{score  === 1 &&
        <h3>
         "Keep practicing/Don't quit your day job!"
        </h3>
      }
	  			{score === 2 &&
        <h3>
		"Good enough to be in a Jonas Bros cover band!"
        </h3>
      }
	  {score === 3 &&
        <h3>
       "I too gig Wednesday nights at shady bars..."
        </h3>
      }


	  				{score === 4 &&
        <h2>
          "Good work! Might even consider watching if there is no cover charge!"
        </h2>
      }

		{score === 5 &&
        <h2>
         "Can I have you autograph and some money?""
        </h2>
      }
	  </div>

				
			) : (
				<>
					<ReactAudioPlayer
						className="sound"
						style={
							currentQuestion === 1 ? { display: "block" } : { display: "none" }
						}
						src={D}
						autoPlay
						controls
					/>
					<div className="question-section">
						<img
							style={
								currentQuestion === 0
									? { display: "block" }
									: { display: "none" }
							}
							className="chord"
							src={Chord1}
							logo
						/>

						<img
							style={
								currentQuestion === 3
									? { display: "block" }
									: { display: "none" }
							}
							className="scale"
							src={a}
							logo
						/>


						<div className="question-count">
							<span>Question {currentQuestion + 1}</span>/{questions.length}
							<div className="score-section">
								{score} out of {questions.length} correct
								
							</div>
						</div>
						<div className="question-text">
							{questions[currentQuestion].questionText}
						</div>
					</div>
					<div className="answer-section">
						{questions[currentQuestion].answerOptions.map((answerOption) => (
							<button
								onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}
							>
								{answerOption.answerText}
							</button>

						))}
					
						
					</div>
				</>
			)}
		</div>
	);
}
export default Guitarquiz;

